import styled from "styled-components";
import { mainStyle } from "../../../styles/GlobalStyle";
import imgFace from "../../../../img/imgFace.png";
import React from "react";
import "aos/dist/aos.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGlobe, faLink } from "@fortawesome/free-solid-svg-icons";

export const Section2 = () => {
  const tool = ["Javascript", "HTML5", "CSS3", "React"];

  return (
    <>
      <Wrap>
        <InWrap>
          <Title>Hello, I'm Heejeong</Title>
          <Profile>
            <BoxWrap>
              <Box className="question">Technology Stacks..?</Box>
              <Box className="answer">
                {tool.map((a) => (
                  <h1 key={a} className={`technic`}>
                    {a}
                  </h1>
                ))}
              </Box>
            </BoxWrap>
            <Img
              style={{
                background: `url(${imgFace}) no-repeat center/cover`,
              }}
            />
            <BoxWrap>
              <Box className="question">Your site..?</Box>

              <Box className="site">
                <a href="#!">
                  Git Hub
                  <FontAwesomeIcon icon={faLink} />
                </a>
              </Box>
              <Box className="site">
                <a href="#!">
                  E-MAIL
                  <FontAwesomeIcon icon={faGlobe} />
                </a>
              </Box>
            </BoxWrap>
          </Profile>
          <TextWrap>
            <Text>
               ,     
              <span>""</span>.
              <br />
               <span>Frontend</span>     
              .        ,
                      .
              <br />
              <br />
                    ,   
                      
              .     
                     . 
                   .
            </Text>
          </TextWrap>
        </InWrap>
      </Wrap>
    </>
  );
};

const Wrap = styled.section`
  width: 100%;
  height: 100vh;
  padding: 0 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100vh;
`;

const InWrap = styled.div`
  width: 100%;
  height: 80vh;
  padding: 50px 100px;
  background-color: ${mainStyle.mainColor};
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const Title = styled.div`
  width: 100%;
  padding-bottom: 20px;
  text-align: center;
  font-size: 25px;
  font-weight: 900;
  border-bottom: 2px solid ${mainStyle.blackColor};
`;

const Profile = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Img = styled.div`
  width: 30%;
  height: 300px;
`;

const BoxWrap = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Box = styled.div`
  text-align: center;
  position: relative;
  border-radius: 0.6em;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  color: ${mainStyle.subColor};
  &.question {
    background: ${mainStyle.backColor};
    padding: 20px 100px;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 0;
      height: 0;
      border: 7px solid transparent;
      border-right-color: ${mainStyle.backColor};
      border-left: 0;
      margin-top: -7px;
      margin-left: -7px;
    }
  }
  &.answer {
    background: ${mainStyle.pointColor};
    padding: 20px 100px;
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 0;
      height: 0;
      border: 7px solid transparent;
      border-left-color: ${mainStyle.pointColor};
      border-right: 0;
      margin-top: -7px;
      margin-right: -7px;
    }
    img {
      display: block;
    }
  }
  h1 {
    padding: 2px;
  }
  &.site {
    background: ${mainStyle.blackColor};
    padding: 20px 100px;
    transition: 0.5s;
    animation: blink 1s step-end infinite;
    @keyframes blink {
      50% {
        opacity: 0.8;
      }
    }
    &:hover {
      background: ${mainStyle.pointColor};
      &::after {
        border-left-color: ${mainStyle.pointColor};
      }
      animation: none;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 0;
      height: 0;
      border: 7px solid transparent;
      border-left-color: ${mainStyle.blackColor};
      border-right: 0;
      margin-top: -7px;
      margin-right: -7px;
      transition: 0.5s;
    }
  }
  svg {
    margin-left: 10px;
  }
`;

const TextWrap = styled.div``;

const Text = styled.div`
  font-family: "IBM Plex Sans KR", sans-serif;
  color: ${mainStyle.blackColor};
  font-weight: 900;
  font-size: 20px;
  line-height: 30px;
  span {
    font-size: 30px;
    color: ${mainStyle.backColor};
  }
`;
